
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
props:{
'detailInfo':{
type : Object,
required : true
},
'status':{
type : Boolean,
required : true
},
'assignedList':{
type : Array,
required : true
}
},
data(){
return{
acceptStandard:false,
storyshow:true,
technicalAnalysis:false,
approvalOpinion:false,
show8:false,
user:''

}
},
directives: {
TransferDom
},
components: {
Group,
Cell,
Datetime,
CellBox,
Popup,
XInput
},
methods:{
change(value){

this.$emit('date',value)

},
changeUser(){
this.show8 = !this.show8;
},
getUser(item){
this.show8 = false;
this.$emit("userId",item)
this.user = item.nickname;
}
}

}



