import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {formatTime} from '../maps/date.js'
export default {
props:{
'detailInfo':{
type : Object,
required : true
},
'status':{
type : Boolean,
required : true
},
'assignedList':{
type : Array,
required : true
}
},
data(){
return{
acceptStandard : false,
storyshow : true,
technicalAnalysis : false,
approvalOpinion : false,
show8:false,
user:''
}
},
directives: {
TransferDom
},
components: {
Group,
Cell,
Datetime,
CellBox,
Popup

},
methods:{
formatTime,
change(value){

this.$emit('date',value)

},
changeUser(){
console.log("111");
this.show8 = !this.show8;
},
getUser(item){
this.show8 = false;
this.$emit("userId",item)
this.user = item.nickname;
}
},
created(){
}


}



